import Vue from 'vue';
import Vuex from 'vuex'
import products from '@/data/products'
import request from '@/helpers/request';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartProducts: [
      {productId: 1, amount: 2}
    ],
    cartProductsData: [],
    accessKey: null,
    isLoadingProducts: true,
    orderInfo: null
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCartProduct(state) {
      state.cartProducts = [];
      state.cartProductsData = []
    },
    updateCartProductAmount(state, {productId, amount}) {
      const item = state.cartProducts.find((item) => item.productId === productId)

      if (item) {
        item.amount = amount;
      }
    },
    changeAmount(state, {amount, productId}) {
      const item = state.cartProducts.find((item) => productId === item.productId)

      if (item) {
        item.amount = amount;
      }
    },
    deleteProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((item) => item.productId !== productId)
    },
    updateAccessKey(state, accessKey) {
      state.accessKey = accessKey;
    },
    updateCartProducts(state, cart) {
      state.cartProductsData = cart;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity
      }))
    },
    updateLoadingProducts(state, isLoading) {
      state.isLoadingProducts = isLoading
    }
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((p) => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url
          }
        }
      })
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, {amount, product}) => {
        acc += amount * product.price
        return acc;
      }, 0)
    },
    getOrderBasket(state) {
      if (!state.orderInfo) return []

      return state.orderInfo.basket.items;
    }
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return request.get('orders/' + orderId, {
        params: {
          userAccessKey: context.state.accessKey
        }
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data)
        })
    },
    loadCart(context) {
      context.commit('updateLoadingProducts', true)

      request.get('baskets', {
        params: {
          userAccessKey: context.state.accessKey
        }
      })
      .then((resp) => {
        const data = resp.data;

        if (!context.state.accessKey) {
          localStorage.setItem('accessKey', data.user.accessKey);
          context.commit('updateAccessKey', data.user.accessKey);
        }

        context.commit('updateCartProducts', data.items);
        context.commit('syncCartProducts');
      })
        .finally(() => context.commit('updateLoadingProducts', false))
    },
    addProductToCart(context, {productId, amount}) {
      return request.post('baskets/products', {
        productId,
        quantity: amount
      },
        {
          params: {
            userAccessKey: context.state.accessKey
          }
        }
      )
        .then((resp) => {
          context.commit('updateCartProducts', resp.data.items);
          context.commit('syncCartProducts');
        })
    },
    updateCartProductAmount(context, {productId, amount}) {
      context.commit('updateCartProductAmount', {productId, amount})

      if (amount < 1) {
        return
      }

      return request
        .put('/baskets/products', {
          productId,
          quantity: amount
        }, {
          params: {
            userAccessKey: context.state.accessKey
          }
        })
        .then((resp) => {
          context.commit('updateCartProducts', resp.data.items)
        })
        .catch(() => {
          context.commit('syncCartProducts')
        })
    },
    deleteCartProduct(context, productId) {
      context.commit('deleteProduct', productId)

      return request
        .delete('/baskets/products', {
          data: {
            productId
          },
          params: {
            userAccessKey: context.state.accessKey
          }
        })
        .then((resp) => {
          context.commit('updateCartProducts', resp.data.items)
        })
        .catch(() => {
          context.commit('syncCartProducts')
        })
    }
  }
})
