<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click.prevent="incrementAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.product.price * item.amount | formatNumber }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteCartProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import formatNumber from '@/helpers/formatNumber';
import { mapActions, mapMutations } from 'vuex';

export default {
  name: 'CartItem',
  props: ['item'],
  filters: {formatNumber},
  methods: {
    ...mapActions(['deleteCartProduct']),
    incrementAmount() {
      this.amount += 1;
    },
    decrementAmount() {
      this.amount = this.amount > 0 ? this.amount - 1 : 0;
    },
  },
  computed: {
    amount: {
      get() {
        return this.item.amount
      },

      set(value) {
        this.$store.dispatch('updateCartProductAmount',
          {productId: this.item.productId, amount: value}
        )
      }
    }
  }
};
</script>

