<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :categories="categories" :colors="colors" :filter.sync="filter"></ProductFilter>
      <section class="catalog">
        <div class="catalog__loading loading" v-if="isLoading">
          <div class="loading__content">
            <img src="/img/spinner.gif" alt="Loading" class="loading__spinner">
            <p>Loading...</p>
          </div>
        </div>
        <div v-if="isError">Произошла ошибка </div>
        <ProductList :products="productsOnPage"></ProductList>
        <BasePagination :pages-count="pagesCount" v-model="page"></BasePagination>
      </section>
    </div>
  </main>
</template>

<script>
import ProductFilter from '@/components/ProductFilter';
import BasePagination from '@/components/BasePagination';
import ProductList from '@/components/ProductList';
import request from '@/helpers/request';

export default {
  name: 'MainPage',
  components: { ProductFilter, BasePagination, ProductList },
  data() {
    return {
      perPage: 3,
      page: 1,
      pagesTotal: 0,
      products: [],
      categories: [],
      colors: [],
      isLoading: true,
      isError: false,
      fetchedProducts: null,
      filter: {
        priceFrom: null,
        priceTo: null,
        categoryId: null,
        colorId: null
      }
    }
  },
  methods: {
    getProducts() {
      this.isLoading = true;

      return request.get('products', {
        params: {
          page: this.page,
          limit: this.perPage,
          categoryId: this.filter.categoryId,
          colorId: this.filter.colorId,
          minPrice: this.filter.priceFrom || null,
          maxPrice: this.filter.priceTo || null
        }
      })
        .then((resp) => {
          this.fetchedProducts = resp.data;
          this.pagesTotal = resp.data.pagination.total
        })
        .catch(() => this.isError = true)
        .finally(() => this.isLoading = false)
    },
    getCategories() {
      return request.get('productCategories')
        .then((resp) => {
          this.categories = resp.data.items;
        })
    },
    getColors() {
      return request.get('colors')
        .then((resp) => this.colors = resp.data.items)
    }
  },
  computed: {
    productsOnPage() {
      return this.fetchedProducts
        ? this.fetchedProducts.items.map((product) => {
          return {
            ...product,
            image: product.image.file.url
          }
        })
        : []
    },
    pagesCount() {
      return Math.ceil(this.pagesTotal / this.perPage)
    },
    filteredProducts() {
      return this.products.filter((product) => {
        if (this.filter.priceFrom > 0 && product.price < this.filter.priceFrom) {
          return false
        }

        if (this.filter.priceTo > 0 && product.price > this.filter.priceTo) {
          return false
        }

        if (this.filter.categoryId && this.filter.categoryId !== product.categoryId) {
          return false;
        }

        return !(this.filter.colorId && !product.colors.includes(this.filter.colorId));
      })
    }
  },
  created() {
    Promise.all([
      this.getProducts(),
      this.getCategories(),
      this.getColors()
    ])
  },
  watch: {
    page() {
      this.getProducts();
    },
    filter() {
      this.getProducts()
    }
  }
};
</script>

