<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page <= 1}" href="#" aria-label="Предыдущая страница" @click.prevent="goPrev">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pagesCount" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}"  @click.prevent="goToPage(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page >= pagesCount}" href="#" aria-label="Следующая страница" @click.prevent="goNext">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'VPagination',
  props: ['page', 'pagesCount'],
  model: {
    prop: 'page',
    event: 'change-page'
  },
  methods: {
    goPrev() {
      if (this.page <= 1) return;
      this.$emit('change-page', this.page - 1)
    },
    goNext() {
      if (this.page >= this.pagesCount) return;
      this.$emit('change-page', this.page + 1)
    },
    goToPage(pageNumber) {
      this.$emit('change-page', pageNumber)
    }
  }
};
</script>

