<template>
  <div class="catalog__loading loading" v-if="$store.state.isLoadingProducts">
    <div class="loading__content">
      <img src="/img/spinner.gif" alt="Loading" class="loading__spinner">
      <p>Loading...</p>
    </div>
  </div>

  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="submitCart">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" :error="formError.name" v-model="formData.name" placeholder="Введите ваше полное имя" />
            <BaseFormText title="Адрес доставки" :error="formError.address" v-model="formData.address" placeholder="Введите ваш адрес" />
            <BaseFormText title="Телефон" :error="formError.phone" v-model="formData.phone" placeholder="Введите ваш телефон" />
            <BaseFormText title="Email" :error="formError.email" v-model="formData.email" placeholder="Введи ваш Email" />
            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" checked="">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <img v-if="isLoading" src="/img/spinner.gif" alt="Loading" class="loading__spinner">
          <ul class="cart__orders">
            <OrderCartRow
              v-for="item in products"
              :name="item.product.title"
              :id="item.productId"
              :key="item.productId"
              :price="item.product.price"
            />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>3</b> товара на сумму <b>{{totalPrice | formatNumber}} ₽</b></p>
          </div>

          <button v-if="products.length" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="globalErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ globalErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormField from '@/components/BaseFormField';
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import { mapGetters } from 'vuex';
import OrderCartRow from '@/components/OrderCartRow'
import formatNumber from '@/helpers/formatNumber';
import request from '@/helpers/request';

export default {
  name: 'OrderPage',
  components: {
    OrderCartRow,
    BaseFormText,
    BaseFormField,
    BaseFormTextarea
  },
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'})
  },
  filters: {
    formatNumber
  },
  data() {
    return {
      formData: {},
      formError: {},
      isLoading: false,
      globalErrorMessage: null
    }
  },
  methods: {
    submitCart() {
      this.formError = {}
      this.isLoading = true

      request
        .post('orders', {...this.formData}, {
          params: {
            userAccessKey: this.$store.state.accessKey
          },
        })
        .then((response) => {
          this.$store.commit('resetCartProduct')
          this.$store.commit('updateOrderInfo', response.data)
          this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {}
          this.globalErrorMessage = error.response.data.error.message;
        })
        .finally(() => this.isLoading = false)
    }
  }
};
</script>

